import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import PaymentTermUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/payment/paymentTerm/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/payment-terms"

class UpdatePaymentTerm extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editPaymentTerm } = this.props;
    const { id } = this.props.match.params;
    editPaymentTerm(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.paymentTerm.error) {
      notification("error", this.props.paymentTerm.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.paymentTerm.success) {
      this.props.history.replace(redirectTo);
    }
  }

  handleSubmit = value => {
    const { updatePaymentTerm } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing location code
    value.discount = value.discount / 100;
    updatePaymentTerm(id, value);
  };

  render() {
    // console.log(this.props.paymentTerm);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="paymentTerms.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.paymentTerm.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <PaymentTermUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                paymentTerm={this.props.paymentTerm}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    paymentTerm: state.paymentTerm
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdatePaymentTerm);
