import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Radio, Divider, Row, Col , Alert, InputNumber } from "antd";

//const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class PaymentTermUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    //console.log(this.props.paymentTerm);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { paymentTerm } = this.props;
    const { loading } = paymentTerm;
    const data = paymentTerm.payment_term ? paymentTerm.payment_term : [];
    // var phone = data.phone + "";
    // phone = phone.substring(2);
    const used = data.is_used;
    const error = paymentTerm.errorField ? paymentTerm.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = { 
      display: "flex", 
      width: "100%" 
    }

    const radioButtonStyle = { 
      flex: "1", 
      textAlign: "center" 
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="paymentTerms.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="paymentTerms.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="paymentTerms.add.code" />}
              validateStatus={
                error.payment_term_code || getFieldError("payment_term_code")
                  ? "error"
                  : null
              }
              help={
                error.payment_term_code
                  ? error.payment_term_code
                  : getFieldError("payment_term_code")
              }
            >
              {getFieldDecorator("payment_term_code", {
                initialValue: data.payment_term_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="name" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input disabled={used ? true : false} />)}
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="isCash" />}
                >
                  {getFieldDecorator("is_cash", {
                    initialValue: data.is_cash,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(
                    <Radio.Group
                      buttonStyle="solid"
                      style={radioGroupStyle}
                    >
                      <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="yes" /></Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="no" /></Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="paymentTerms.add.default" />}
                >
                  {getFieldDecorator("is_default", {
                    initialValue: data.is_default ? data.is_default : 0,
                  })(
                    <Radio.Group
                      buttonStyle="solid"
                      style={radioGroupStyle}
                    >
                      <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="yes" /></Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={0}><IntlMessages id="no" /></Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

           

            {/* <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="paymentTerms.add.discount" />}
            >
              {getFieldDecorator("discount", {
                initialValue: data.discount ? data.discount*100 : 0,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                ]
              })(
                <InputNumber
                  disabled={used ? true : false}
                  min={0}
                  max={100}
                  formatter={value => `${value}%`}
                  parser={value => value.replace('%', '')}
                />
              )}
            </Form.Item> */}
          {/* </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}> */}
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="paymentTerms.add.minimumPaymentDay" />}
                >
                  {getFieldDecorator("minimum_payment_day", {
                    initialValue: data.minimum_payment_day,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      //disabled={used ? true : false}
                      style={{ width:"100%" }}
                      precision={0}
                      min={0}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="paymentTerms.add.netPaymentDay" />}
                >
                  {getFieldDecorator("net_payment_day", {
                    initialValue: data.net_payment_day,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                     // disabled={used ? true : false}
                      style={{ width:"100%" }}
                      precision={0}
                      min={0}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
            
            
            

            
            
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedPaymentTermUpdateForm = Form.create({ name: "update_payment_term" })(PaymentTermUpdateForm);

export default WrappedPaymentTermUpdateForm;
